import React, {Component} from 'react';

class NotFound extends Component {

  render() {
    return (
      <div className='not-found'>
        <h1>
          Not All Who Wander Are Lost, But You Sure As Hell Are.
        </h1>
      </div>
    );
  }
}

export default NotFound;


